div.forecast {
    position: relative;
    height: 200px;
    width: 150px;
    background-color: rgba(256, 256, 256, 0.2);
    text-align: center;
    padding: 10px;
    margin: 20px;
    cursor: pointer;
}

div.wrapper {
    z-index: 10;
}

div.forecast p {
    position: relative;
    margin: 5px;
    font-size: 20px;
    color: white;
    z-index: -1;
}

div.forecast img {
    position: relative;
    margin: 20px 0;
    height: 50px;
    z-index: -1;
}
